import { faAngleLeft, faAngleRight, faCrown } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';

// images
import rugby from "../assets/imgs/bgs/rugby.jpg";

import Header from '../header/Header';
import Footer from '../footer/Footer';
import { formatMoney, query, request } from '../common/Utils';

class Benefits extends Component {
  state = {
    referrals: [],
    referralDescriptions: [
      "Register and get a DSTV package with premium subscription",
      "Bring in 5 persons and get a cash reward",
      "Grow your downlines to 25 persons, get 3 months DSTV premium subcription with cash reward",
      "Grow your downlines to 125 persons get 3 months DSTV premium subscription with cash reward",
      "Grow your downlines to 625 persons get 6 months DSTV premium subscription, Club souvenirs with cash reward",
      "Grow your downlines to 3,125 persons get 9 months DSTV premium subscription, win an all expense paid trip to home ground of your favourite club to watch a live match with cash reward",
      "Grow your downlines to 15,625 persons CHAMPION Exit the tournament with cash reward",
    ],
    position: 0
  }

  translateBackward = () => {
    if (this.state.position <= -1600) return;
    let pos = this.state.position - 260;
    this.setState({ position: pos });
    query("#benefitSlide0")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide0")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide0")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide1")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide1")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide1")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide2")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide2")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide2")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide3")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide3")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide3")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide4")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide4")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide4")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide5")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide5")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide5")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide6")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide6")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide6")[0].style.WebkitTransitionDuration = '0.5s';
  }

  translateFront = () => {
    if (this.state.position >= 0) return;
    let pos = this.state.position + 260;
    this.setState({ position: pos });
    query("#benefitSlide0")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide0")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide0")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide1")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide1")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide1")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide2")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide2")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide2")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide3")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide3")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide3")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide4")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide4")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide4")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide5")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide5")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide5")[0].style.WebkitTransitionDuration = '0.5s';
    query("#benefitSlide6")[0].style.WebkitTransform = `translate(${pos}px)`;
    query("#benefitSlide6")[0].style.WebkitTransitionProperty = 'all';
    query("#benefitSlide6")[0].style.WebkitTransitionDuration = '0.5s';
  }

  componentDidMount() {
    this.getReferralLevels();
  }

  getReferralLevels = async () => {
    try {
      let resp = await request('/api/referrallevels');
      let referrallevels = resp.status === true ? resp.data : [];
      this.setState({ referrals: referrallevels });
    } catch (error) {
      console.log(error);
    }
  }


  render() {
    return (
      <>

        {/* Header */}
        <Header page='/benefits'></Header>

        <section>
          {/* Title */}
          <div className='position-relative d-block'>
            <img className='' src={rugby} style={{ objectFit: 'cover', objectPosition: 'center', width: '100%', maxHeight: '200px' }} alt="" />
            <div className='position-absolute top-0 start-0 bg-white-50 h-100 w-100'>
              <h5 className="position-absolute text-light top-50 start-50 translate-middle fw-bold fs-1">Benefits</h5>
            </div>
          </div>
        </section>


        {/* Who we Are */}
        <section className="container py-5">

          <div className=''>

            <div>
              <p className='mb-4 text-muted text-center lh-lg'>
                <span>
                  Benefits accruable to members is consequent on their ability to recruit other individuals to become members of Sports Fans CLUB. SFC apply the principles of Multilevel marketing and Network marketing to create a channel of sales for other sporting products manufactured by partner companies. There is a maximum of seven (7) levels in the tournament, each level completed has the following benefits.
                </span>
                <br />
                <span><b>A 10% CHARITY AND SUSTAINABILITY FEE IS DEDUCTABLE STARTING FROM LEVEL 4 (SUPPORTERS)</b></span>
              </p>
            </div>

          </div>

          <div>

            <div className="d-flex justify-content-between align-items-center">
              <button onClick={() => this.translateFront()} className="btn btn-sm mx-lg-2 mx-1 btn-primary"><FontAwesomeIcon icon={faAngleLeft}></FontAwesomeIcon></button>

              <div className='d-flex justify-content-start h-100 overflow-auto mb-5'>
                {
                  this.state.referrals.map((level, index) => (

                    <div id={`benefitSlide${index}`} key={index} className='col-lg-4 col-md-6 col-11 card shadow-sm my-lg-3 mx-lg-3 mx-1 py-lg-4'>
                      <div className='card-body h-100'>

                        <h2 className='fs-3 text-center text-primary mb-5'>
                          <FontAwesomeIcon icon={faCrown}></FontAwesomeIcon> <br />
                          {level.rank}
                          <hr />
                        </h2>

                        <div className='text-center fs-4 text-secondary'>
                          {this.state.referralDescriptions[index]} <br /><br />
                          <span className='fs-1 fw-bold'>{formatMoney(level.cash_benefit, 0)}</span>
                        </div>

                        <p className='fs-1 fw-bold text-center text-secondary'>
                          <span></span>
                        </p>

                      </div>
                    </div>

                  ))
                }
              </div>

              <button onClick={() => this.translateBackward()} className="btn btn-sm mx-lg-2 mx-1 btn-primary"><FontAwesomeIcon icon={faAngleRight}></FontAwesomeIcon></button>
            </div>

          </div>

        </section>


        {/* Footer */}
        <Footer></Footer>

      </>
    );
  }
}

export default Benefits;